import { createContext, useMemo, useReducer } from 'react'
import { createSlice } from '@reduxjs/toolkit'

const initialState = () => ({
  keymapHandles: [],
  selectedHandleIndex: null,
  activeKeymap: {
    handleIndex: null,
    keymapRaw: null,
    keymapParsed: null,
    metadataParsed: null
  },
  importModalOpen: false,
  feedbackModalOpen: false,
  pendingImports: null
})

const slice = createSlice({
  name: 'filesystem-picker',
  initialState,
  reducers: {
    openImportModal (state) { state.importModalOpen = true },
    closeImportModal (state) { state.importModalOpen = false },
    openFeedbackModal (state) { state.feedbackModalOpen = true },
    closeFeedbackModal (state) { state.feedbackModalOpen = false },
    selectFilesForImport (state, action) {
      const { handles } = action.payload
      state.pendingImports = handles
    },
    saveHandles (state, action) {
      state.keymapHandles.push(...action.payload.handles)
    },
    selectHandle (state, action) {
      const { index } = action.payload
      state.selectedHandleIndex = index
    },
    selectKeymap (state, action) {
      const { handleIndex, keymapRaw, keymapParsed, metadataParsed } = action.payload
      state.activeKeymap = {
        handleIndex,
        keymapRaw,
        keymapParsed,
        metadataParsed
      }
    },
    updateActiveKeymap (state, action) {
      const { keymapParsed, keymapRaw } = action.payload
      state.activeKeymap.keymapParsed = keymapParsed
      state.activeKeymap.keymapRaw = keymapRaw
    }
  }
})

function useActionDispatchers (dispatch) {
  return useMemo(() => (
    Object.keys(slice.actions).reduce((actions, action) => {
      actions[action] = (...args) => dispatch(slice.actions[action](...args))
      return actions
    }, {})
  ), [dispatch])
}

export function useFileSystemStore () {
  const [state, dispatch] = useReducer(slice.reducer, null, initialState)
  const dispatchers = useActionDispatchers(dispatch)

  return [state, dispatchers]
}

export const FileSystemContext = createContext([{}, {}])

export function FileSystemStore ({ children }) {
  const [state, dispatchers] = useFileSystemStore()

  return (
    <FileSystemContext.Provider value={[state, dispatchers]}>
      {children}
    </FileSystemContext.Provider>
  )
}

export function withFileSystemStore (Component) {
  return props => (
    <FileSystemStore>
      <Component {...props} />
    </FileSystemStore>
  )
}
